body {
  --tw-bg-opacity: 1;
  background-color: rgba(243, 244, 246, var(--tw-bg-opacity));
}

.app-container {
  width: 100%;
}

@media (min-width: 640px) {
  .app-container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .app-container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .app-container {
    max-width: 1024px;
  }
}

@media (min-width: 1280px) {
  .app-container {
    max-width: 1280px;
  }
}

@media (min-width: 1536px) {
  .app-container {
    max-width: 1536px;
  }
}

.app-container {
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
  margin-left: auto;
  margin-right: auto;
  max-width: 1280px;
}

.up-shadow {
  --tw-shadow: 0 0 8px -2px rgba(0, 0, 0, 0.5), 0 4px 6px -2px rgba(0, 0, 0, 0.25);
  box-shadow: 0 0 #0000, 0 0 #0000, var(--tw-shadow);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.empty-content {
  font-size: 1.125rem;
  line-height: 1.75rem;
  text-align: center;
  transform: translateY(50vh);
}

.translate-item {
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.translate-item-label {
  margin-right: 1rem;
  --tw-text-opacity: 1;
  color: rgba(156, 163, 175, var(--tw-text-opacity));
}

.translate-item-value {
  flex-grow: 1;
  --tw-text-opacity: 1;
  color: rgba(17, 24, 39, var(--tw-text-opacity));
}
